@mixin button {

  &:hover {
  }
}

@mixin background {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
* {
  image-rendering: -webkit-optimize-contrast;
}

.main-template{
  & > div:nth-child(1){
   padding-top: 65px;
  }

  & > div:last-child{
    padding-bottom: 80px;
  }

  font-size: 13px;
  line-height: 2;
}

body{
  font-family: "Open Sans";
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  color: #767676;
  font-size: 13px;
  margin: 0 !important;
}
.container{
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  height: 100%;
  padding: 0 30px;
  //@include breakpoint(small only){
  //  width: 100%;
  //}
  //@include breakpoint(medium only){
  //  width: 100%;
  //}

}

.content{
  width: 95%;
}

header{
  position: relative;
}

header{
  padding: 0 !important;
}

.price{
  line-height: 1;
  .p{
    font-size: 70px;
    font-weight: 500;
  }
  .period{
    font-size: 20px;
    font-weight: 500;
  }
}



p, h1,h2,h3,h4,h5, h6{

    margin: .5rem 0;

}

.padding-bottom{
  padding-bottom: 25px;
}


.padding-top{
  padding-top: 11px;
}

body {
  background-image: url(../images/swish-bg.png);
   background-size: 161% 71%;
  background-repeat: no-repeat;
  background-position: top center;
}

